@charset "utf-8";
*{margin:0px;padding:0px;}
.img_box .movie_img {
    width:430px;
    height:932px;
    margin:0px auto;
    animation: ani01 infinite 8s;;
}
@keyframes ani01{
    0%{transform: scale(1);}
    50%{transform: scale(1.5);}
    100%{transform: scale(1);}
}
.grid_wrap {
    display:grid;
    grid-gap: 100px;
    grid-template-columns: repeat(2,minmax(400px,1fr));
    width:80%;
    padding:100px 100px 50px 200px; 
}

article{
    /* width:44%; */
    min-width:450px;
    min-height:340px;
    /* height:200px; */
    display: flex;
    /* display:inline-block; */
    box-shadow: 0px 4px 12px rgba(0,0,0,.6);
    /* margin:100px 40px; */
    border-radius: 6px;
}
.img{
    width:190px;
    height:300px;
    margin:0px 5% 30px 30px;
    /* margin-top:0px; */
    box-shadow: 0px 0px 12px rgba(0,0,0,.4);
    transform: translateY(-20px);
}
article >div{
    padding:30px 20px;
    /* display: flex; */
}
article >div> p{
    padding:10px 0px;
}

@media screen and (max-width: 1090px){
    .grid_wrap {
        display:grid;
        grid-gap: 100px;
        grid-template-columns: repeat(1,minmax(400px,1fr));
        width:80%;
        padding:100px 100px 50px 100px; 
    }
}